.cover-container{
    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 100vh;
}

.video{
    object-fit: cover;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1;

}
.cover-container > .cover-h1 {
    color: #ffffff;
    text-align: center;
    margin-bottom: 250px;
    font-size: 100px;
    padding: 0 25px 0 25px;
}
.cover-container > .cover-p {
    margin-top: 8px;
    color: white;
    font-size: 32px;
}